.cart-container {
    display: flex;
    justify-content: center;
    min-height: 75vh;
}

@media screen and (min-width: 768px) {
    .cart-container {
        min-height: 90vh;
    }
}

.cart-content-container {
    display: flex;
    flex-direction: column;
    width: 90%;
    max-width: 1110px;
}

@media screen and (min-width: 768px) {
    .cart-content-container {
        width: 80%;
        max-width: 1110px;
    }
}

.cart-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.remove-all-btn {
    border: none;
    outline: none;
    background-color: transparent;
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 16px;
    color: #0b69ff;
    cursor: pointer;
}

.cart-heading {
    color: #3e4c59;
    font-family: 'Roboto';
    font-size: 24px;
    font-weight: 700;
}

@media screen and (min-width: 768px) {
    .cart-heading {
        font-size: 48px;
    }
}

